.scatter-plot {
  position: relative; }
  .scatter-plot ul {
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0; }
    .scatter-plot ul li {
      position: absolute;
      width: 50px;
      height: 50px;
      background-color: rgba(0, 0, 0, 0.5);
      overflow: hidden;
      border-radius: 50%; }
      .scatter-plot ul li span {
        position: absolute;
        width: 100%;
        text-align: center;
        top: 50%;
        -webkit-transform: translateY(-50%);
            -ms-transform: translateY(-50%);
                transform: translateY(-50%); }
  .scatter-plot svg {
    position: absolute;
    top: 0;
    left: 0; }
    .scatter-plot svg line {
      stroke: #000;
      stroke-width: 1; }



